@import url(./common.scss);

main {
  height: 85.8%;
  padding-top: 64px;
  padding-bottom:55px;
}

/* find method */

.find_method {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 80px;
  background: #eee;

  li {
    overflow: hidden;
    flex: 1;

  a {
      display: flex;
      flex-direction: column;
      align-items: center;
      color: #513736;
      transition: transform 0.3s linear 0s;

      &:hover {
        transform: scale(1.05);
      }

      svg {
        width:22px;
        height:22px;
        margin: 5px 0;
      }
    }
  }
}

/* recommend section */

.recommend_section {
  .recommend_header {
    height: 22px;
    line-height: 22px;
    text-indent: 15px;
    color: #8e9399;
  }

  ul{
    li {
      height: 54px;
      line-height: 54px;
      border-top: 1px solid #ebebeb;
      border-bottom: 1px solid #ebebeb;
      text-align: center;
      color: #8e9399;
      font-size: 14px;
    }
  } 
}


.tab_bar {
  ul {
    li:nth-child(3) {
      a {
        color: #513736;
        } 
      }

      li:nth-child(2) {
        a {
          color: #c0c1c2;
          } 
        }

        li:nth-child(1) {
          a {
            color: #c0c1c2;
            } 
          }

          li:nth-child(4) {
            a {
              color: #c0c1c2;
              } 
            }

  }
}