@import url(./common.scss);

main {
  height: 85.8%;
  padding-top: 64px;
  padding-bottom:55px;

.search_box {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 7.3%;
  min-height: 53px;
}

.search_inner {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 88%;
  height: 50%;
  background-color: #f1f2f4;
  border-radius: 5px;
    svg {
      width:16px;
      height: 16px;
      color: #bebcc1;
      align-self: center;
      margin-left: 15px;
    }

    input {
      flex: 1;
      background: transparent;
      border: none;
      padding: 0 15px;

      &::placeholder {
        text-align: center;
      }

    }
  
}

.main_section {
    .section_header {
      height: 32px;
      line-height: 32px;
      border: 1px solid #ebebeb;
      border-left: none;
      border-right: none;
      color: #b6b5b1;
      padding-left: 15px;

  
    }
    .friends_list {
      display: flex;
      flex-direction: column;
      padding-bottom:55px;
      li {
        a{
          display: flex;
          flex-direction: row;
          align-items: center;
          font-size: 16px; 
        }
      }
    }
  
  


  .profile_name {
    flex: auto;
    text-align: left;
  }

  .profile_messages {
    height: 30px;
    line-height: 30px;
    margin-right: 15px;
    padding: 0 10px;
    border-radius: 5px;
    background: #BFB3A8;
    font-size: 10px;
    color: #F2F2F2;
  }
}

}

.tab_bar {
  ul {
    li:nth-child(1) {
      a {
        color: #513736;
        } 
      }

      li:nth-child(2) {
        a {
          color: #c0c1c2;
          } 
        }

        li:nth-child(3) {
          a {
            color: #c0c1c2;
            } 
          }

          li:nth-child(4) {
            a {
              color: #c0c1c2;
              } 
            }

  }
}